@import url('https://fonts.googleapis.com/css2?family=Inter:wght@600&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@700&display=swap');
*{
    margin: 0;
    padding: 0;
}

.skillsPagewrapper{
    min-height: 100vh;
    /* width: 100vw; */
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: rgb(13, 2, 31);
    position: sticky;
    top:0;
    /* box-shadow: inset 0px 0px 20px 1px  #ea9ceb; */
    
}



.skillheading{
    font-family: 'Roboto Mono', monospace;
    font-size: 5rem;
    color: rgba(255,255,255,0);
    -webkit-text-stroke: white 2px;
    text-transform: uppercase;
    margin: 0 20px;
    transition: color 0.2s ease-in;

}
.skillheading:hover{
    color: rgba(255,255,255,1);
    
}
.splinediv{
    position: absolute;
    width: 100%;
    height: 100%;
    filter:brightness(0.95);
}
.blob{
    transform: scale(1);
}
.blob canvas{
    transform: scale(1);
}

@media (max-width: 905px){
    .blob{
        transform: scale(0.9);
    }
    .blob canvas{
        transform: scale(0.9);
    }
}
@media (max-width: 650px){
    .blob{
        transform: scale(0.8);
    }
    .blob canvas{
        transform: scale(0.8);
    }
}
