@import url('https://fonts.googleapis.com/css2?family=Inter:wght@600&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Jost:wght@800&display=swap');
.TSpagewrapper{
    background:linear-gradient( rgb(13, 2, 31), rgba(23, 2, 32, 0.7),rgba(23, 2, 32, 0.7),rgb(13, 2, 31) ), url('https://media1.giphy.com/media/l3mZnuz4coJp8EBBm/giphy.gif?cid=ecf05e47cfihrbp1bs0081g1c5zq0vk5e5tlpc59t6j9jy9o&rid=giphy.gif&ct=g');
    background-size: cover;
    height: 100vh;
    /* min-height: 100vh; */
    /* width: 100vw; */
    color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: sticky;
    top:0;
}
.introOverlay{
    height: 100%;
    min-height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    backdrop-filter: blur(8px);
    font-family: 'Jost', sans-serif;
    z-index: 2;
    
    /* box-shadow: inset 0px 0px 20px 1px  #ea9ceb; */
}
.skillheaderdiv{
    margin: 10px 0 10px 0;
    text-align: center;
}

.skillheader{
    color: rgb(252, 156, 241);
    font-size: 5rem;
    font-family: 'Jost', sans-serif;
}

.techiconswrap{
    width: 60%;
    height: 70%;
    max-height: 150%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    backdrop-filter: blur(15px);
    border-top: 1px solid rgba(255,255,255,0.5);
    border-right: 1px solid rgba(255,255,255,0.5);
    box-shadow: 8px 12px 50px rgba(0,0,0,0.4);
    background-color: rgba(131, 129, 129, 0.1);
    -webkit-backdrop-filter: blur(8px);
    border-radius: 10px;
    margin-top: 10px;
}
.iconsrow{
    height: 15%;
    width: 100%;
    /* margin: 10% 0; */
    display: flex ;
    justify-content: space-around;
    /* background: white; */
}
.logobg{
    width: fit-content;
    padding: 0 8px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgb(252, 156, 241,0);
    border-radius: 10px;
    transition: all 0.2s;
}
.logobg:hover{
    background: rgb(252, 156, 241,1);
    box-shadow: rgb(255, 213, 250) 5px 5px 10px;
    color: rgb(0, 0, 0);
}
.logoimg{
    width: 70px;
}

.Voverlay{
    position: absolute;
    z-index: 3;
    /* transform: scale(); */
}

@media (max-width:780px){
    .TSpagewrapper{
        position: static;
        background:linear-gradient( rgb(13, 2, 31), rgba(23, 2, 32, 0.7),rgba(23, 2, 32, 0.7),rgb(13, 2, 31) ), url('https://media1.giphy.com/media/l3mZnuz4coJp8EBBm/giphy.gif?cid=ecf05e47cfihrbp1bs0081g1c5zq0vk5e5tlpc59t6j9jy9o&rid=giphy.gif&ct=g');
        background-size: cover;
        height: 100%;
        min-height: 150vh;
    }
    
    .introOverlay{
        background: rgba(13, 2, 31,1);
        padding: 0 0 50px 0;
    }
    .skillheader{
        font-size: 3.5rem;
    }
    .techiconswrap{
        /* align-items: center; */
        justify-content: flex-start;
        padding: 10px 0;
        height: 100%;
        min-height: 110%;
    }
    .iconsrow{
        flex-direction: column;
        /* justify-content: center; */
        align-items: center;
        height: 100%;
    }
    .logobg{
        /* width: 150px; */
        margin: 20px 0;
    }
    .logobg i{
        font-size: 150px;
    }
    .logoimg{
        width: 100%;
    }
}
@media (max-width:350px){
    .skillheader{
        font-size: 3rem;
    }
}